* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: var(--primary-color);
}

:root {
    --primary-color: rgb(255, 255, 255);
    --secondary-color: rgb(0, 17, 27);
    --tertiary-color: rgb(90, 86, 86);
    --fourth-color: rgb(143, 210, 241);
}

.main {
    background-color: var(--secondary-color);
}

