/* add styling */

/* header styling */

.header {
    padding: 5px 35px;
    background-color: var(--tertiary-color);
    display: flex;
    justify-content: space-between;
    top: 0;
    color: var(--primary-color);
}

.logo {
    font-family: 'New Tegomin', serif;
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 2.7em;
    text-transform: uppercase;
}

.logo a {
    color: var(--primary-color);
}

.logo a:hover {
    text-decoration: none;
    color: var(--fourth-color);
}

.header-nav {
    display: flex;
}

.header-link {
    text-decoration: none;
    color: var(--primary-color);
    text-transform: uppercase;
    padding: 5px 15px;
    font-weight: lighter;
    font-size: 1.5em;
    text-align: center;
}

.header-link:hover {
    color: var(--fourth-color);
    text-decoration: none;
}

.nav-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;
}

.hero {
    background-image: url("./assets/images/tech-against-trafficking-hero.jpg");
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    background-position: 80%;
    padding: 80px;
}

/* About Styling */

.section-header {
    background-color: var(--secondary-color);
    padding: 20px;
}

.section-header h2 {
    text-transform: uppercase;
    font-family: 'New Tegomin', serif;
}

.about-flex-container {
    padding: 10px;
    margin: 10px;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
}

.bio {
    padding: 10px;
    margin: 10px;
    display: flex;
    font-weight: lighter;
    color: var(--primary-color);
    font-style: italic;
    font-size: 25px;
    text-align: center;
}

.image-holder {
    justify-content: center;
    text-align: center;
    align-items: center;
    width: 40%;
}

img {
    width: 60%;
}

/* contact section */

.contact-padding {
    padding: 10px;
}

.contact-label {
    padding-right: 10px;
}

input {
    color: black;
    font-size: 1rem;
    padding: 0.25rem;
    border: 1px solid var(--dark);
    outline: none;
    border-radius: 3px;
}

input:focus {
    border: 3px solid var(--fourth-color);
}

.form-padding {
    padding-bottom: 5px;
}

.error-message {
    text-transform: uppercase;
}

form {
    width: 40%;
}

textarea {
    width: 600px;
    height: 120px;
    border: 3px solid #cccccc;
    padding: 5px;
    color: black;
}

textarea:focus {
    border: 4px solid var(--fourth-color);
}

