.button {
    font-size: 1rem;
    /* text-decoration: none; */
    text-transform: uppercase;
    padding: 0.25rem 1rem;
    border-radius: 3px;
    border: none;
    outline: none;
    cursor: pointer;
    margin: 0 1px;
    background-color: var(--tertiary-color);
}

button:hover {
    background-color: var(--fourth-color);
}

/* footer styling */

.footer {
    background-color: var(--tertiary-color);
    display: flex;
    text-align: center;
    justify-content: center;
    padding: 20px;
}

.footer-words {
    font-weight: lighter;
    color: var(--primary-color);
    font-style: italic;
    font-size: 25px;
}

.footer-span {
    font-size: 30px;
    letter-spacing: 5px;
    font-family: 'New Tegomin', serif;
    display: block;
}

/* media queries */

@media screen and (max-width: 768px) {
   
    header {
        display: flex;
        flex-wrap: wrap;
    }
    .logo {
        font-size: xx-large;
    }
    .header {
        justify-content: center; 
    }
    .hero {
        display: none;
    }
    .nav-list {
        justify-content: center;
        flex-wrap: nowrap;
        font-size: xx-small;
        
    }
    .nav-list li {
        padding: 0px;
        margin: 0px;
    }

    .image-holder {
        width: 100%;
    }

    .footer span {
        font-size: 20px;
        letter-spacing: 0;
    }
    .footer-words {
        font-size: 18px;
    }
    textarea {
        width: 300px;
    }

}